<template>
  <div id="trash" class="detail">
    <div class="note-sidebar">
      <h3 class="notebook-title">回收站</h3>
      <div class="menu">
        <div>更新时间</div>
        <div>标题</div>
      </div>
      <ul class="notes">
        <li v-for="note in trashNotes">
          <router-link :to="`trash?noteId=${note.id}`">
            <span class="date">{{ note.updatedAtFriendly }}</span>
            <span class="title">{{ note.title }}</span>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="note-detail">
      <div class="note-bar" v-if="currentTrashNote">
        <span>创建日期： {{currentTrashNote.createdAtFriendly}}</span>
        <span> | </span>
        <span>更新日期： {{currentTrashNote.updatedAtFriendly}}</span>
        <span> | </span>
        <span> 所属笔记本：{{belongTo}}</span>

        <a class="btn action" @click="onRevert">恢复</a>
        <a class="btn action" @click="onDelete">彻底删除</a>
      </div>
      <div class="note-title">
        <span>{{currentTrashNote.title}}</span>
      </div>
      <div class="editor">
        <div class="preview markdown-body" v-html="compliedMarkdown"></div>
      </div>
    </div>
  </div>
</template>

<script>
import MarkdownIt from 'markdown-it'
import {mapGetters, mapMutations, mapActions} from 'vuex'

let md = new MarkdownIt()

export default {
  name: "TrashDetail",
  data(){
    return{}
  },
  created() {
    // 如果没有登录，点击回收站，自动跳转到登录页面
    this.checkLogin({path: '/login'})
    this.getNotebooks()
    this.getTrashNotes()
      .then(() => {
        this.setCurrentTrashNote({currentTrashNoteId: this.$route.query.noteId})
        this.$router.replace({path: `trash?noteId=${this.currentTrashNote.id}`})
      })
  },
  computed: {
    ...mapGetters([
      'trashNotes',
      'currentTrashNote',
      'belongTo'
    ]),
    compliedMarkdown(){
      return md.render(this.currentTrashNote.content || '')
    }
  },
  methods: {
    ...mapMutations([
      'setCurrentTrashNote',
    ]),
    ...mapActions([
      'checkLogin',
      'deleteTrashNote',
      'revertTrashNote',
      'getTrashNotes',
      'getNotebooks'
    ]),
    onRevert(){
      this.revertTrashNote({noteId: this.currentTrashNote.id})
        .then(()=>{ // 选中的恢复成功后，立即选中回收站的第一项
          this.setCurrentTrashNote({})
          this.$router.replace({path: `trash?noteId=${this.currentTrashNote.id}`})
        })
      console.log('revert')
    },
    onDelete() {
      this.$confirm('删除后将无法恢复', '是否从回收站彻底删除该笔记？', {
        confirmButtonText: '确定，心意已决',
        cancelButtonText: '我再想想',
        type: 'warning'
      }).then(() => {
        this.deleteTrashNote({noteId: this.currentTrashNote.id})
      }).then(()=>{ // 选中的删除成功后，立即选中回收站的第一项
        this.setCurrentTrashNote({})
        this.$router.replace({path: `trash?noteId=${this.currentTrashNote.id}`})
      })
      console.log('delete')
    },
  },
  beforeRouteUpdate(to, from, next) { // 切换回收站的过程中展示对应的内容
    this.setCurrentTrashNote({currentTrashNoteId: to.query.noteId})
    next()
  }
}
</script>

<style lang="less" scoped>
@import "../assets/css/note-sidebar.less";
@import "../assets/css/note-detail.less";
#trash {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;

  .note-bar {
    .action {
      float: right;
      margin-left: 10px;
      padding: 2px 4px;
      font-size: 12px;
    }
  }
}

</style>



